import React from 'react'
import {
  Row,
  Col,
  Collapse,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  TabContent,
  TabPane,
  FormText
} from 'reactstrap'
import classnames from 'classnames'
import { Edit, Trash, ChevronDown } from 'react-feather'

class MsgHistory extends React.Component {
  state = {
    activeTab: '1',
    collapseID: '',
    status: 'Closed',
    collapseItems: this.props.collapseItems,
    colorOption: this.props.colorOption
  }

  toggleTab = (tab) => {
    if (this.state.activeTab !== tab) {
      this.setState({ activeTab: tab })
    }
  }

  toggleCollapse = (collapseID) => {
    this.setState((prevState) => ({
      collapseID: prevState.collapseID !== collapseID ? collapseID : ''
    }))
  }

  onEntered = (id) => {
    if (id === this.state.collapseID) this.setState({ status: 'Opened' })
  }
  onEntering = (id) => {
    if (id === this.state.collapseID) this.setState({ status: 'Opening...' })
  }

  onExited = (id) => {
    if (id === this.state.collapseID) this.setState({ status: 'Closed' })
  }

  onExiting = (id) => {
    if (id === this.state.collapseID) this.setState({ status: 'Closing...' })
  }
  formatme = (val) => {
    const title = this.state?.colorOption?.[val]
      ? this.state.colorOption[val]
      : val
    return title
  }
  getContent(e) {
    let nomin = ''
    e?.nominees?.length &&
      e.nominees.forEach((n, idx) => {
        if (idx === e.nominees.length - 1) {
          nomin = nomin + n?.name
        } else {
          nomin = nomin + n?.name + ', '
        }
      })
    const content = (
      <div>
        <Row>
          <Col md="5" sm="12">
            <FormText>
              <b> Type : </b> {e.type}
              <br />
              <b> Description : </b> {e.desc}
              <br />
              <b> Date : </b> {e.date.split('T')[0]}
              <b> Time : </b> {e.time}
            </FormText>
          </Col>
          <Col md="5" sm="12">
            <FormText>
              <b>Status :</b> ' ' <br />
              <b>Nominees :</b> {nomin}
            </FormText>
          </Col>
          <Col md="2" sm="12">
            <FormText>
              <b>
                {' '}
                <Edit
                  className="cursor-pointer mr-1"
                  size={window.screen.width < 600 ? 13 : 20}
                  onClick={() => {
                    console.log('event', e)
                  }}
                />
              </b>
              <br />
              <b>
                <Trash
                  className="cursor-pointer"
                  size={window.screen.width < 600 ? 13 : 20}
                  onClick={() => {
                    console.log('event', e)
                  }}
                />
              </b>
            </FormText>
          </Col>
        </Row>
      </div>
    )
    console.log('asdfgh', content)
    return content
  }
  render() {
    const accordionMarginItems =
      this?.state?.collapseItems?.length > 0 ? (
        this.state.collapseItems.map((collapseItem) => {
          return (
            <div className="collapse-margin" key={collapseItem._id}>
              <Card
                onClick={() => this.toggleCollapse(collapseItem._id)}
                className={classnames({
                  'collapse-collapsed':
                    this.state.status === 'Closed' &&
                    this.state.collapseID === collapseItem._id,
                  'collapse-shown':
                    this.state.status === 'Opened' &&
                    this.state.collapseID === collapseItem._id,
                  closing:
                    this.state.status === 'Closing...' &&
                    this.state.collapseID === collapseItem._id,
                  opening:
                    this.state.status === 'Opening...' &&
                    this.state.collapseID === collapseItem._id
                })}
              >
                <CardHeader>
                  <CardTitle className="lead collapse-title collapsed">
                    {/* Message:{this.formatme(collapseItem.type)} Subject: */}
                    {this.formatme(collapseItem.title)}
                  </CardTitle>
                  <ChevronDown size={15} className="collapse-icon" />
                </CardHeader>
                <Collapse
                  isOpen={collapseItem._id === this.state.collapseID}
                  onEntering={() => this.onEntering(collapseItem._id)}
                  onEntered={() => this.onEntered(collapseItem._id)}
                  onExiting={() => this.onExiting(collapseItem._id)}
                  onExited={() => this.onExited(collapseItem._id)}
                >
                  <CardBody>{this.getContent(collapseItem)}</CardBody>
                </Collapse>
              </Card>
            </div>
          )
        })
      ) : (
        <span style={{ fontSize: '12px', marginTop: '-10px' }}>
          No Messages Found
        </span>
      )

    return (
      <React.Fragment>
        <Card
          style={{
            paddingBottom: '1.5rem',
            paddingRight: '0.5rem',
            marginBottom: this.props.page === 'portfolio' ? '2.2rem' : '1rem'
          }}
        >
          <CardHeader>
            <CardTitle>{this.props.heading}</CardTitle>
          </CardHeader>

          <CardBody
            className="card_body"
            style={{
              maxHeight:
                this.props.page === 'portfolio'
                  ? '260px'
                  : ' calc(100vh - 435px)'
            }}
          >
            <TabContent activeTab={this.state.activeTab}>
              <TabPane tabId="1">
                <div className="vx-collapse">{accordionMarginItems}</div>
              </TabPane>
              <TabPane className="component-code" tabId="2">
                hey
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </React.Fragment>
    )
  }
}
export default MsgHistory
