import React from 'react'
import { Row, Col, Card, CardHeader, CardTitle, CardBody } from 'reactstrap'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'
import MsgHistory from './MessageHistory'
import { ToastContainer } from 'react-toastify'
import { connect } from 'react-redux'
import Select from 'react-select'
import VideoRecorder from 'react-video-recorder'
import { Recorder } from 'react-voice-recorder'
import 'react-voice-recorder/dist/index.css'
import Spinner from '../../components/@vuexy/spinner/Loading-spinner'
import classnames from 'classnames'
import {
  scheduleNewMessage,
  getScheduledMessages
} from '../../redux/actions/sender/index'
import { getData, addData } from '../../redux/actions/data-list/'
import { Input, Button, FormGroup, Label } from 'reactstrap'
import '../../assets/scss/pages/data-list.scss'
import '../../assets/scss/components/app-loader.scss'
import { toast } from 'react-toastify'
import Sidebar from '../Nominee/DataListSidebar'

import themeConfig from '../../configs/themeConfig'
const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}
const colourOptions1 = [
  {
    value: 'Attachment',
    label: 'Attachment',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Text',
    label: 'Text',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Voice',
    label: 'Voice',
    color: '#0052CC',
    isFixed: true
  },
  {
    value: 'Video',
    label: 'Video',
    color: '#0052CC',
    isFixed: true
  }
]
const currdt = new Date()
currdt.setHours(currdt.getHours() + 1)
class ContactUs extends React.Component {
  state = {
    date: currdt.toISOString().substr(0, 10),
    time: currdt.toTimeString().split(' ')[0],
    desc: '',
    type: '',
    title: '',
    file: '',
    sidebar: false,
    currentData: null,
    isLoading: false,
    selectNomineeRef: React.createRef(),
    nomineeOption: [
      {
        value: 'Add Nominee',
        label: 'Add Nominee',
        color: '#00B8D9',
        isFixed: false
      }
    ],
    nominees: [],
    nomineeList: [],
    audioDetails: {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    }
  }
  componentDidMount() {
    this.props.getScheduledMessages()
    !this?.props?.dataList?.data?.length &&
      !this.props.listLoading &&
      this.props.getData()
  }
  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }
  static getDerivedStateFromProps(props, state) {
    if (props.dataList.data.length !== state.nomineeList.length) {
      const nomineeOption = state.nomineeOption
      props.dataList.data.forEach((e) => {
        const temp = {
          value: e._id,
          label: e.name + ' - ' + e.relation,
          color: '#00B8D9',
          isFixed: false
        }
        nomineeOption.push(temp)
      })

      return {
        nomineeList: props.dataList.data,
        nomineeOption: nomineeOption
      }
    }
    return null
  }
  handleAudioStop = (data) => {
    this.setState({ audioDetails: data })
  }
  handleAudioUpload = (file) => {
    console.log(file)
  }

  handleRest = () => {
    const reset = {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    }
    this.setState({ audioDetails: reset })
  }
  addData = async (obj) => {
    this.setState({ isLoading: true })
    try {
      await this.props.addData(obj)
      this.setState({ isLoading: false })
      this.handleSidebar(false, true)
    } catch {
      this.setState({ isLoading: false })
      toast.error('Add Nominee Failed! Please contact admin')
    }
  }
  changeNominee = (event) => {
    const nominees = []
    if (event && event?.[0].value === 'Add Nominee') {
      this.setState({ sidebar: true })
    } else {
      event &&
        event.forEach((e) => {
          nominees.push({ id: e.value, name: e.label })
        })
      !event && this.setState({ isAddAssetButtonDisabled: true })
      this.setState({ nominees })
    }
  }
  render() {
    const { desc, type, title, date, time, file, nominees } = this.state
    return (
      <div
        className={`data-list ${
          this.props.thumbView ? 'thumb-view' : 'list-view'
        }`}
      >
        <Sidebar
          show={this.state.sidebar}
          data={this.state.currentData}
          updateData={this.updateData}
          addData={this.addData}
          isLoading={this.state.isLoading}
          handleSidebar={this.handleSidebar}
          thumbView={this.props.thumbView}
          getData={this.props.getData}
          dataParams={this.props.parsedFilter}
          addNew={this.state.addNew}
        />
        <div
          className={classnames('data-list-overlay', {
            show: this.state.sidebar
          })}
          onClick={() => this.handleSidebar(false, true)}
        />
        <h2 className="warning spacing nodisplay">Sender</h2>
        <Card>
          <CardHeader>
            <CardTitle>Schedule a Message to Send</CardTitle>
          </CardHeader>
          <CardBody>
            <Row>
              <Col lg="8" md="6" sm="12">
                <Row className="match-height">
                  <Col md="12">
                    <Row>
                      <Col>
                        <FormGroup className="form-label-group">
                          <Select
                            className="React"
                            classNamePrefix="select"
                            name="color"
                            options={colourOptions1}
                            ref={type}
                            isClearable={true}
                            placeholder="Select Message Type..."
                            onChange={(e) => {
                              this.setState({
                                type: e ? e.value : ''
                              })
                            }}
                          />
                        </FormGroup>
                      </Col>
                      <Col>
                        <FormGroup className="form-label-group">
                          <Select
                            isMulti
                            name="nominee"
                            options={this.state.nomineeOption}
                            ref={this.state.selectNomineeRef}
                            className="React"
                            classNamePrefix="select"
                            placeholder="Select Nominee"
                            id="nominee"
                            onChange={this.changeNominee}
                            //onChange={(e) => {{this.setState({nominee: e ? e.value : undefined})}} }
                          />
                          <Label>Select Nominee</Label>
                        </FormGroup>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <FormGroup className="form-label-group">
                          <Input
                            type="text"
                            id="title"
                            className="input-label"
                            placeholder="Title"
                            value={title}
                            onChange={(e) => {
                              this.setState({
                                title: e.target.value
                              })
                            }}
                          />
                          <Label
                            for="title"
                            className={
                              themeConfig.theme === 'dark'
                                ? 'dark-label'
                                : 'light-label'
                            }
                          >
                            Title
                          </Label>
                        </FormGroup>
                      </Col>
                      <Col className="d-flex ">
                        <FormGroup
                          className="form-label-group"
                          style={{ width: '50%' }}
                        >
                          <Input
                            type="date"
                            className="input-label"
                            name="date"
                            id="exampleDate"
                            placeholder="Enter Date"
                            maxLength={10}
                            onChange={(e) =>
                              this.setState({ date: e.target.value })
                            }
                            value={date}
                          />
                          <Label
                            for="exampleDate"
                            className={
                              themeConfig.theme === 'dark'
                                ? 'dark-label'
                                : 'light-label'
                            }
                          >
                            Date *
                          </Label>
                        </FormGroup>
                        <FormGroup
                          className="form-label-group"
                          style={{ width: '49%', marginLeft: '1%' }}
                        >
                          <Input
                            type="time"
                            className="input-label"
                            name="time"
                            id="exampleTime"
                            placeholder="Time"
                            onChange={(e) =>
                              this.setState({ time: e.target.value })
                            }
                            value={time}
                          />
                          <Label
                            for="exampleTime"
                            className={
                              themeConfig.theme === 'dark'
                                ? 'dark-label'
                                : 'light-label'
                            }
                          >
                            Time *
                          </Label>
                        </FormGroup>
                      </Col>
                    </Row>
                    {this.state.type === 'Attachment' && (
                      <FormGroup
                        className="form-label-group"
                        //onChange={(e) => {{this.setState({nominee: e ? e.value : undefined})}} }
                      >
                        <Input
                          type="file"
                          name="file"
                          id="exampleFile"
                          className="input-label form-control"
                          placeholder="File"
                          onChange={(e) =>
                            this.setState({ file: e.target.value })
                          }
                          value={file}
                        />
                        <Label
                          for="exampleFile"
                          className={
                            themeConfig.theme === 'dark'
                              ? 'dark-label'
                              : 'light-label'
                          }
                        >
                          File *
                        </Label>
                      </FormGroup>
                    )}
                    {this.state.type === 'Video' && (
                      <div className="video-recorder">
                        <VideoRecorder
                          onRecordingComplete={(videoBlob) => {
                            console.log('videoBlob', videoBlob)
                          }}
                        />
                      </div>
                    )}
                    {this.state.type === 'Voice' && (
                      <div className="voice-recorder">
                        <Recorder
                          record={true}
                          title={'New recording'}
                          audioURL={this.state.audioDetails.url}
                          showUIAudio
                          handleAudioStop={(data) => this.handleAudioStop(data)}
                          handleAudioUpload={(data) =>
                            this.handleAudioUpload(data)
                          }
                          handleReset={() => this.handleRest()}
                        />
                      </div>
                    )}
                    <FormGroup className="form-label-group">
                      <Input
                        type="textarea"
                        placeholder="Description"
                        className="input-label"
                        rows="5"
                        id="description"
                        value={desc}
                        onChange={(e) => {
                          this.setState({
                            desc: e.target.value
                          })
                        }}
                      />
                      <Label
                        for="description"
                        className={
                          themeConfig.theme === 'dark'
                            ? 'dark-label'
                            : 'light-label'
                        }
                      >
                        Description
                      </Label>
                    </FormGroup>
                    <div
                      className="d-flex justify-content-end"
                      style={{ textAlign: 'right' }}
                    >
                      <Button.Ripple
                        outline
                        className="button-label"
                        color="secondary"
                        onClick={() => {
                          this.setState({
                            title: '',
                            type: '',
                            desc: '',
                            date: '',
                            time: ''
                          })
                        }}
                      >
                        Reset
                      </Button.Ripple>
                      <Button.Ripple
                        className="button-label"
                        color="warning"
                        onClick={() => {
                          this.props.scheduleNewMessage({
                            desc,
                            type,
                            title,
                            date,
                            time,
                            nominees
                          })

                          this.setState({
                            title: '',
                            type: '',
                            desc: '',
                            date: '',
                            time: ''
                          })
                        }}
                        disabled={title.length > 0 ? false : true}
                      >
                        Add
                      </Button.Ripple>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <ToastContainer />
                  <Col>
                    {this.props.loadingmsg ? (
                      <Spinner color="danger" size="sm" />
                    ) : (
                      <MsgHistory
                        heading={'Messages History'}
                        collapseItems={this.props.messages.reverse()}
                        colorOption={this.props?.messages?.colorOption}
                      />
                    )}
                  </Col>
                </Row>
              </Col>
              <Col lg="4" md="6" sm="12">
                <div className="swipermobile">
                  <Swiper {...params}>
                    <div
                      style={{
                        backgroundImage: `url(${img1})`,
                        backgroundSize: 'contain'
                      }}
                    ></div>
                    <div
                      style={{
                        backgroundImage: `url(${img2})`,
                        backgroundSize: 'contain'
                      }}
                    ></div>
                    <div
                      style={{
                        backgroundImage: `url(${img3})`,
                        backgroundSize: 'contain'
                      }}
                    ></div>
                    <div
                      style={{
                        backgroundImage: `url(${img4})`,
                        backgroundSize: 'contain'
                      }}
                    ></div>
                    <div
                      style={{
                        backgroundImage: `url(${img5})`,
                        backgroundSize: 'contain'
                      }}
                    ></div>
                  </Swiper>
                </div>
              </Col>
            </Row>
          </CardBody>
        </Card>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  console.log('this.props.loadingmsg ', state, state)
  return {
    dataList: state.dataList,
    listLoading: state.dataList.totalRecordsLoading,
    messages: state.sender.getmesssages,
    loadingmsg: state.sender.loadingmsg
  }
}
export default connect(mapStateToProps, {
  scheduleNewMessage,
  getData,
  addData,
  getScheduledMessages
})(ContactUs)
